module.exports = ".goal-container {\n  width: 276px;\n  display: flex;\n  margin-top: 12px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-size: 16px;\n}\n\n\n/* Hide the browser's default checkbox */\n\n\n.goal-container input {\n  opacity: 0;\n  cursor: pointer;\n  height: 0;\n  width: 0;\n}\n\n\n.goal-text {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  margin-left: 10px;\n  font-size: 16px;\n  max-height: 25px;\n  line-height: 25px;\n}\n\n\n.goal-text-done {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  max-height: 25px;\n  line-height: 25px;\n  text-decoration: line-through;\n}\n\n\n/* Create a custom checkbox */\n\n\n.check-mark {\n  min-height: 22px;\n  max-height: 22px;\n  min-width: 22px;\n  max-width: 22px;\n  border-radius: 2px;\n  border: solid 1px #c4c7cc;\n  background-color: #f9fbfd;\n}\n\n\n/* Create the checkmark/indicator (hidden when not checked) */\n\n\n.check-mark:after {\n  content: \"\";\n  display: none;\n}\n\n\n/* On mouse-over, add a grey background color */\n\n\n.goal-container:hover input ~ .check-mark {\n  background-color: #ccc;\n}\n\n\n/* When the checkbox is checked, add a green background and border radius 4px */\n\n\n.goal-container input:checked ~ .check-mark {\n  background-color: #39b54a;\n  border-radius: 4px;\n}\n\n\n/* Show the check-mark when checked */\n\n\n.goal-container input:checked ~ .check-mark:after {\n  display: block;\n}\n\n\n/* Style the check-mark/indicator */\n\n\n.goal-container .check-mark:after {\n  margin-left: 7px;\n  margin-top: 3px;\n  width: 6px;\n  height: 10px;\n  border: solid white;\n  border-width: 0 2px 2px 0;\n  -webkit-transform: rotate(45deg);\n}\n\n\n.no-padding {\n  padding: 0;\n  margin-left: 0;\n}\n\n\n.add-goal-input {\n  border-radius: 0;\n  border: 0 !important;\n  border-bottom: 1px solid #13aa57 !important;\n  outline: none !important;\n  -webkit-appearance: none;\n  padding-left: 0 !important;\n  box-shadow: none !important;\n}\n\n\n.add-goal-input:focus {\n  border-bottom: 2px solid #13aa57 !important;\n}\n\n\n.edit-button {\n  cursor: pointer;\n  color: #13aa57;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"