module.exports = "/* Customize the label (the container) */\n.goal-container {\n  width: 276px;\n  display: flex;\n  margin-top: 12px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-size: 16px;\n}\n/* Hide the browser's default checkbox */\n.goal-container input {\n  opacity: 0;\n  cursor: pointer;\n  height: 0;\n  width: 0;\n}\n.goal-text {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  margin-left: 10px;\n  font-size: 16px;\n  max-height: 25px;\n  line-height: 25px;\n}\n.goal-text-done {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  max-height: 25px;\n  line-height: 25px;\n  text-decoration: line-through;\n}\n/* Create a custom checkbox */\n.check-mark {\n  min-height: 22px;\n  max-height: 22px;\n  min-width: 22px;\n  max-width: 22px;\n  border-radius: 2px;\n  border: solid 1px #c4c7cc;\n  background-color: #f9fbfd;\n}\n/* Create the checkmark/indicator (hidden when not checked) */\n.check-mark:after {\n  content: \"\";\n  display: none;\n}\n/* On mouse-over, add a grey background color */\n.goal-container:hover input ~ .check-mark {\n  background-color: #ccc;\n}\n/* When the checkbox is checked, add a green background and border radius 4px */\n.goal-container input:checked ~ .check-mark {\n  background-color: #39b54a;\n  border-radius: 4px;\n}\n/* Show the check-mark when checked */\n.goal-container input:checked ~ .check-mark:after {\n  display: block;\n}\n/* Style the check-mark/indicator */\n.goal-container .check-mark:after {\n  margin-left: 7px;\n  margin-top: 3px;\n  width: 6px;\n  height: 10px;\n  border: solid white;\n  border-width: 0 2px 2px 0;\n  -webkit-transform: rotate(45deg);\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"