module.exports = "@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700,800&display=swap&subset=cyrillic');\n\n.active-link {\n  color: #13aa57 !important;\n}\n\nimg {\n  display: inline-block;\n  height: auto;\n  margin-left: 20px;\n  width: 102px;\n  float: left;\n}\n\n.lower-navbar {\n  background-color: #fff;\n  box-shadow: 0 -18px 52px 30px #f4f5f7;\n  height: 82px;\n  position: fixed;\n  margin: 0 auto;\n  top: 41px;\n  width: 100%;\n  z-index: 2000;\n}\n\n.lower-navbar .navbar-wrapper {\n  height: -webkit-fit-content;\n  height: -moz-fit-content;\n  height: fit-content;\n  margin: 10px auto 0;\n  width: 100%;\n}\n\n.lower-navbar ul {\n  background-color: rgb(239, 237, 241);\n  list-style: none;\n  margin: 0;\n  overflow: hidden;\n  padding: 0;\n}\n\n.lower-navbar li a {\n  display: block;\n  color: #000000;\n  font-family: 'Open Sans';\n  font-size: 16px;\n  font-weight: 600;\n  font-stretch: normal;\n  font-style: normal;\n  line-height: normal;\n  letter-spacing: normal;\n  padding: 20px 20px;\n  text-align: center;\n  text-decoration: none;\n}\n\n/* menu */\n\n.lower-navbar .menu {\n  clear: both;\n  max-height: 0;\n  transition: max-height .2s ease-out;\n}\n\n/* menu icon */\n\n.lower-navbar .menu-icon {\n  cursor: pointer;\n  display: inline-block;\n  float: right;\n  padding: 28px 20px;\n  position: relative;\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none;\n}\n\n.lower-navbar .menu-icon .navicon {\n  background: #13aa57;\n  border-radius: 2px;\n  display: block;\n  height: 2px;\n  position: relative;\n  transition: background .2s ease-out;\n  width: 28px;\n}\n\n.lower-navbar .menu-icon .navicon:before,\n.lower-navbar .menu-icon .navicon:after {\n  background: #13aa57;\n  border-radius: 2px;\n  content: '';\n  display: block;\n  height: 100%;\n  position: absolute;\n  transition: all .2s ease-out;\n  width: 100%;\n}\n\n.lower-navbar .menu-icon .navicon:before {\n  top: 10px;\n}\n\n.lower-navbar .menu-icon .navicon:after {\n  top: -10px;\n}\n\n/* menu btn */\n\n.lower-navbar .menu-btn {\n  display: none;\n}\n\n.lower-navbar .menu-btn:checked ~ .menu {\n  max-height: 372px;\n}\n\n.lower-navbar .menu-btn:checked ~ .menu-icon .navicon {\n  background: transparent;\n}\n\n.lower-navbar .menu-btn:checked ~ .menu-icon .navicon:before {\n  transform: rotate(-45deg);\n}\n\n.lower-navbar .menu-btn:checked ~ .menu-icon .navicon:after {\n  transform: rotate(45deg);\n}\n\n.lower-navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,\n.lower-navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {\n  top: 0;\n}\n\n@media (min-width: 1600px) {\n  .lower-navbar .navbar-wrapper {\n    width: 50% !important;\n    }\n  }\n\n@media (min-width: 1440px) {\n  .lower-navbar .navbar-wrapper {\n    width: 60% !important;\n  }\n}\n\n@media (min-width: 1024px) {\n  .lower-navbar .navbar-wrapper {\n    width: 86% !important;\n  }\n  .lower-navbar li {\n    float: left;\n  }\n  .lower-navbar ul {\n    background-color: #fff;\n  }\n  .lower-navbar li a {\n    padding: 20px 24px;\n  }\n  .lower-navbar .menu {\n    clear: none;\n    float: right;\n    max-height: none;\n  }\n  .lower-navbar .menu-icon {\n    display: none;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"